import './App.css';
import {NavigationBar} from "./Components/NavigationBar";
import DisplayItems from "./Components/DisplayItems";
import DetailedItem from "./Components/DetailedItem"
import NewAccount from "./Components/NewAccount";
import {
    BrowserRouter as Router,
    Switch,
    Route
} from "react-router-dom";
import firebase from "firebase/compat";
import AddItem from "./Components/AddItem";
import AccountSettings from "./Components/AccountSettings";
import LinkToAccount from "./Components/LinkToAccount";

function App() {

    const db = firebase.firestore()

    const handleUpdate = (event) => {
        db.collection('items')
            .doc(event.target.id)
            .update({
                done: event.target.checked
            })
            .catch(err => console.log(err))
    }

    const addItem = async (item) => {
        await db.collection("items")
            .add(item)
            .then(() => console.log('hozzaadva'))
            .catch(err => console.log(err))
        window.location.href = '/'
    };

    const deleteItem = async (itemID) => {
        const db = firebase.firestore()
        await db.collection('items')
            .doc(itemID)
            .delete()
            .catch(err => console.log(err))
        window.location.href = '/'
    }

    const resetApp = () =>{
        localStorage.clear()
        window.location.href = '/'
    }

    return (
        <div className="App" style={{height: '100vh'}}>
            <NavigationBar sx={{position: 'sticky'}}/>
            <Router>
                <Switch>
                    <Route path='/addItem'>
                        <AddItem addItem={addItem}/>
                    </Route>
                    <Route path='/item/:itemID'>
                        <DetailedItem deleteItem={deleteItem}/>
                    </Route>
                    <Route path='/account'>
                        <AccountSettings resetApp={resetApp}/>
                    </Route>
                    <Route path='/linkAccount/:userID'>
                        <LinkToAccount/>
                    </Route>
                    <Route path='/'>
                        {localStorage.getItem('userID') ?
                            <DisplayItems handleUpdate={handleUpdate} sx={{height: '100vh'}}/>
                            : <NewAccount/>}
                    </Route>
                </Switch>
            </Router>
        </div>
    );
}

export default App;
