import React from 'react'
import QRCode from "react-qr-code";
import {Box, Button, Card, CardContent, Typography} from "@mui/material";

export default function AccountSettings(props) {

    const userID = localStorage.getItem('userID')

    const style = {
        card: {
            margin: '16px',
        },
        box: {
            margin: '0 16px 0 16px',
            display: 'flex',
            justifyContent: 'flex-end'
        }
    }

    return (
        <Box>
            <Card sx={style.card}>
                <CardContent >
                    <Typography variant='subtitle1' sx={{marginBottom: '32px'}}>Az azonosítód: {userID}</Typography>
                    <QRCode value={window.location.href + 'linkAccount/' + userID}/>
                </CardContent>
            </Card>
            <Box sx={style.box}>
                <Button variant='contained' href='/' sx={{marginRight: '16px'}}>Vissza</Button>
                <Button variant='contained' onClick={props.resetApp} color='error'>Új lista</Button>
            </Box>
        </Box>
    )
}