import React, {useEffect} from 'react'
import {useParams} from "react-router-dom";

export default function LinkToAccount() {
    const {userID} = useParams()

    useEffect(()=>{
        if(userID){
            localStorage.setItem('userID', userID)
            window.location.href = '/'
        }
    }, [userID])

    return <div/>
}