import React, {useState} from 'react'
import {Box, Button, Card, CardContent, Grid, TextField, Typography} from "@mui/material";
import GenerateRandomCode from 'react-random-code-generator';

export default function NewAccount() {

    const [userID, setUsedID] = useState('')

    const GenerateUserID = () => {
        localStorage.setItem('userID', GenerateRandomCode.TextNumCode(8, 8))
    }

    const submitUserID = () =>{
        localStorage.setItem('userID', userID)
    }

    const style = {
        card: {
            margin: '16px',
        },
        content: {
            height: '100%',
        }
    }

    return (
        <Box>
            <Card sx={style.card}>
                <CardContent sx={style.content}>
                    <Grid container rowSpacing={3} alignItems='center' justifyContent='space-between' direction='column'>
                        <Grid item>
                            <Button variant='contained'
                                    href='/'
                                    onClick={GenerateUserID}
                            >
                                Új lista létrehozása</Button>
                        </Grid>
                        <Grid item>
                            <Typography variant='subtitle1'>Vagy</Typography>
                        </Grid>
                        <Grid item>
                            <Typography variant='subtitle1'>Meglévő lista megnyitása:</Typography>
                            <TextField sx={{width: '100%'}}
                                       onChange={event => setUsedID(event.target.value)}/>
                            <Button href='/' onClick={submitUserID}>Kód használata</Button>
                        </Grid>
                    </Grid>
                </CardContent>
            </Card>
        </Box>
    )
}